
          @import '@/../components/styles/_variables.scss';
          @import '@/../components/styles/_mixins.scss';
        































































.side-nav{
  $side-nav-line-height: 40px;

  flex-shrink: 0;
  width: 240px;
  height: 100%;

  border-right: solid 1px $color-dark-10;

  &__group{
    margin: 0 20px;

    &-label{
      border-bottom: solid 1px $color-dark-10;
      margin: 0;

      color: $color-dark-60;
      font-size: 14px;
      line-height: $side-nav-line-height;
      font-weight: normal;
      text-transform: uppercase;
    }
  }
  &__list{
    list-style: none;
    padding: 0;
    margin: 10px 0 20px;
    line-height: $side-nav-line-height;
  }
  &__link{
    display: block;

    color: $color-dark-80;
    text-decoration: none;

    &:hover{
      color: $color-major;
    }
    &--active{
      background: rgba($color-major, .32);
    }
  }

}
